import React from 'react';
import { useEffect, useState } from "react";
import { Button } from 'antd';
import ImageUploader from 'components/UI/ImageUploader/ImageUploader';
import Heading from 'components/UI/Heading/Heading';
import { AgentPictureUploader, FormTitle } from './AccountSettings.style';
import AdminSectionGrid from 'components/AdminSectionGrid/AdminSectionGrid';
import AxiosFetch from 'library/hooks/AxiosFetch';
import { PostPlaceholder } from 'components/UI/ContentLoader/ContentLoader';

export default function AgentPictureChangeForm() {

  const [tokens, setTokens] = useState([]);

  const fetchData = async () => {
    const url = '/getAllMintedTokens';    
    const res = await AxiosFetch(url);
    if (res.data.success) { 
      setTokens(res.data.data);
    }
  }

  useEffect(fetchData, []);

  return (
    <div>
      <AdminSectionGrid
        data={tokens}
        totalItem={tokens.length}
        columnWidth={[1 / 2, 1 / 2, 1 / 3, 1 / 4]}
        placeholder={<PostPlaceholder />}
        account=""
        updateTokenStaking = {fetchData}
      />
    </div>
  );
}
