import React from 'react';
import { FiExternalLink } from 'react-icons/fi';
import TextLink from 'components/UI/TextLink/TextLink';
import Rating from 'components/UI/Rating/Rating';
import Favourite from 'components/UI/Favorite/Favorite';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import GridCard from './AccountGridCard';

const responsive = {
  desktop: {
    breakpoint: {
      max: 3000,
      min: 1024,
    },
    items: 1,
    paritialVisibilityGutter: 40,
  },
  mobile: {
    breakpoint: {
      max: 464,
      min: 0,
    },
    items: 1,
    paritialVisibilityGutter: 30,
  },
  tablet: {
    breakpoint: {
      max: 1024,
      min: 464,
    },
    items: 1,
    paritialVisibilityGutter: 30,
  },
};

const AccountProductCard = ({
  id,
  collection_id,
  collection_name,
  token_id,
  staking,
  account,
  owner,
  createdAt,  
}) => {

  const gallery = {      
    "title": `Premier#${token_id}`,
    "url": `images/premier/${token_id}.jpg`
  };

  return (
    <GridCard
      isCarousel={true}      
      id={id}
      title={`Premier#${token_id}`}
      price={`${collection_name}`}  
      rating={`${owner}`}
      token_id={token_id}    
      staking={staking}
      account={account}
      date={createdAt}
    >
    
    <img
      src={process.env.REACT_APP_URL + gallery.url}
      alt={gallery.title}
      draggable={false}
      style={{
        width: '100%',
        height: '100%',
        objectFit: 'cover',
        position: 'relative',
      }}
    />
    
    </GridCard>
  );
};

export default AccountProductCard;
