import React from 'react';
import { useEffect, useState } from "react";
import { Button, notification } from 'antd';
import PropTypes from 'prop-types';
import GridCardWrapper, {
  ImageWrapper,
  FavoriteIcon,
  ContentWrapper,
  LocationArea,
  TitleArea,
  PriceArea,
  RatingArea,
  MetaWrapper,
  ButtonGroup,
} from './AccountGridCard.style';


const GridCard = ({
  className,
  id,
  favorite,
  location,
  title,
  price,
  rating,
  editBtn,
  children,
  date,
  staking
}) => {  

  const formatDate = (_date) => {
    var t = _date.split(/[- :]/);

    // Apply each element to the Date function
    var d = new Date(_date);    
    var dateString = d.getUTCFullYear() +"/"+ (d.getUTCMonth()+1) +"/"+ d.getUTCDate() + " " + d.getUTCHours() + ":" + d.getUTCMinutes() + ":" + d.getUTCSeconds();
    return dateString
  }

  return (
    <GridCardWrapper className={`grid_card`.trim()}>
      <ImageWrapper className="media_wrapper">{children}</ImageWrapper>
      <ContentWrapper className="content_wrapper">
        {location && <LocationArea>{location}</LocationArea>}
        {title && <TitleArea>{title}</TitleArea>}
        <MetaWrapper className="meta_wrapper">
          {price && <PriceArea className="price">{price}</PriceArea>}
          {rating && <RatingArea className="rating">Minted By: {rating.substring(0, 6)}...{rating.substring(rating.length - 6)}</RatingArea>}
          {date && <RatingArea className="rating">Mint Date: {formatDate(date)}</RatingArea>}
          <RatingArea className="rating">Staking Status: { staking == 1 ? 'Yes':'No' }</RatingArea>
        </MetaWrapper>
        
      </ContentWrapper>

      {favorite && <FavoriteIcon>{favorite}</FavoriteIcon>}
    </GridCardWrapper>
  );
};

GridCard.propTypes = {
  className: PropTypes.string,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  price: PropTypes.string,
  rating: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  location: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  editBtn: PropTypes.element,
  viewDetailsBtn: PropTypes.element,
};

export default GridCard;
