import React from 'react';
import { FaCamera } from 'react-icons/fa';
import { Upload, Modal,Progress  } from 'antd';
import { ImageUpload } from './imageUploader.style';
import axios from 'axios';

export default class ImageUploader extends React.Component {
  state = {
    previewVisible: false,
    previewImage: '',
    fileList: [      
    ],
    showUploadList: {
      showRemoveIcon: false
    },
    progress: 0,
  };

  handleCancel = () => this.setState({ previewVisible: false });

  handlePreview = (file) => {
    this.setState({
      previewImage: file.url || file.thumbUrl,
      previewVisible: true,
    });
  };

  handleChange = ({ fileList }) => {    
    this.setState({ fileList });
  };

  handleRequest = async (options) => {    
    const { onSuccess, onError, file, onProgress } = options;

    const fmData = new FormData();
    const config = {
      headers: {
        'Access-Control-Allow-Origin': '*',
        "content-type": "multipart/form-data"
      },
      mode:'no-cors',
      onUploadProgress: event => {
        const percent = Math.floor((event.loaded / event.total) * 100);
        this.setState({ progress: percent });
        if (percent === 100) {
          setTimeout(() => this.setState({ progress: 0 }), 1000);
        }
        onProgress({ percent: (event.loaded / event.total) * 100 });
      }
    };
    fmData.append("file", file);
    try {
      const res = await axios.post(
        process.env.REACT_APP_SERVER_URL + "saveFiles",
        fmData,
        config
      );
      
      if(res.data && res.data.success)
        this.props.onChange( res.data.imageName );

      onSuccess("Ok");
    } catch (err) {
      console.log("Error: ", err);
      const error = new Error("Some error");
      onError({ err });
    }

  };
  
  render() {
    const { previewVisible, previewImage, fileList, showUploadList, progress } = this.state;
    const Dragger = Upload.Dragger;

    const uploadButton = (
      <ImageUpload>
        <div className="image-drag-area">
          <FaCamera />
        </div>
        <div className="ant-upload-text">Upload Photos</div>
      </ImageUpload>           
    );
    return (
      <div className="clearfix">
        <Dragger   
          customRequest={this.handleRequest}
          listType="picture-card"
          fileList={fileList}
          showUploadList={showUploadList}
          onPreview={this.handlePreview}
          onChange={this.handleChange}
          className="image_uploader"
        >
          {uploadButton}
        </Dragger>
        <Modal
          visible={previewVisible}
          footer={null}
          onCancel={this.handleCancel}
        >
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </div>
    );
  }
}
